<template>
  <component
    :is="pullRefresh ? 'PullRefresh' : 'div'"
    :failed="failed"
    :disabled="isLoading"
    ref="pullRefresh"
    v-model="refreshing"
    @refresh="refresh"
  >
    <div class="v-infinite-scroll" ref="container"><slot /></div>
    <!-- Âä†ËΩΩ‰∏≠ -->
    <div class="v-infinite-scroll-footer" v-show="value">
      <slot name="loading">
        <Loading class="v-infinite-scroll-loading" size="32" />
      </slot>
    </div>
    <!-- ÂÖ®ÈÉ®Âä†ËΩΩÂÆåÊàê -->
    <div class="v-infinite-scroll-footer" v-show="finished && !empty">
      <slot name="finished">ÂÖ®ÈÉ®Âä†ËΩΩÂÆåÊàê</slot>
    </div>
    <!-- Á©∫Á©∫Â¶Ç‰πü -->
    <div class="v-infinite-scroll-footer" v-show="empty">
      <slot name="empty">
        <div class="v-infinite-scroll-empty">Á©∫Á©∫Â¶Ç‰πü</div>
      </slot>
    </div>
    <!-- Âä†ËΩΩÂ§±Ë¥• -->
    <div class="v-infinite-scroll-footer" v-show="failed" @click="load">
      <slot name="failed">Âä†ËΩΩÂ§±Ë¥•,ÁÇπÂáªÈáçËØï</slot>
    </div>
  </component>
</template>

<script>
import PullRefresh from '../../components/pullRefresh'
import Loading from '../../components/loading'
import Debouncer from '../../utils/debouncer'
import {
  addListener,
  removeListener,
  getScrollEventTarget,
  view
} from '../../utils/shared'

export default {
  name: 'InfiniteScroll',
  components: {
    PullRefresh,
    Loading
  },
  props: {
    value: {
      type: Boolean,
      required: true
    },
    // Âä†ËΩΩÂÆåÊàê (‰∏çÂÜçÊªöÂä®Ëß¶ÂèëÂä†ËΩΩÊï∞ÊçÆ)
    finished: {
      type: Boolean,
      default: false
    },
    // Âä†ËΩΩÂ§±Ë¥•
    failed: {
      type: Boolean,
      default: false
    },
    // È¶ñÊ¨°Âä†ËΩΩÊó∂Ê≤°ÊúâÊï∞ÊçÆÁöÑÁä∂ÊÄÅ
    empty: {
      type: Boolean,
      default: false
    },
    offset: {
      type: Number,
      default: 300
    },
    // ÊòØÂê¶ÂÜÖÁΩÆ‰∏ãÊãâÂà∑Êñ∞ÂäüËÉΩ
    pullRefresh: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      refreshing: false,
      isLoading: false
    }
  },
  watch: {
    value: {
      handler: function(value) {
        value && this.load()
      },
      immediate: true
    }
  },
  mounted() {
    this.bind()
  },
  activated() {
    this.bind()
  },
  deactivated() {
    this.unbind()
  },
  beforeDestroy() {
    this.unbind()
  },
  methods: {
    bind() {
      if (this.isBind) return
      this.$nextTick(() => {
        this.onscroll = new Debouncer(this.scrollHandler)
        this.scrollEl = this.pullRefresh
          ? this.$refs.pullRefresh.scrollEl
          : getScrollEventTarget(this.$el)
        this.container = this.$refs.container
        addListener(this.scrollEl, 'scroll', this.onscroll)
      })
      this.isBind = true
    },
    unbind() {
      removeListener(this.scrollEl, 'scroll', this.onscroll)
      this.isBind = false
      this.scrollEl = null
      this.container = null
      this.onscroll = null
    },
    scrollHandler() {
      if (this.shouldLoadMore()) {
        this.load()
      }
    },
    shouldLoadMore() {
      if (this.finished || this.value || this.failed) return false
      const bottom = this.container.getBoundingClientRect().bottom
      return bottom - view.height() <= this.offset
    },
    load() {
      if (this.finished) {
        this.done()
        return
      }
      // ‰øùËØÅÊúâÂ∫èÂä†ËΩΩÔºåÂêå‰∏ÄÊó∂Èó¥Âè™ÊâßË°å‰∏Ä‰∏™Êìç‰Ωú
      if (this.isLoading || this.refreshing) return
      this.isLoading = true
      this.$emit('input', true)
      this.$emit('load', this.done)
    },
    refresh(done) {
      this.$emit('refresh', done)
    },
    done() {
      this.$nextTick(() => {
        this.$emit('input', false)
        this.isLoading = false
      })
    }
  }
}
</script>

<style lang="postcss">
.v-infinite-scroll-footer {
  width: 100%;
  height: 50px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.v-infinite-scroll-empty {
  min-height: 400px;
  display: flex;
  align-items: center;
}
.v-infinite-scroll-loading {
  color: var(--textSecondary);
}
</style>
